import { Component, OnInit, HostListener, ViewChild, Injector } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { AddTicketCategoryComponent } from '../add-ticket-category/add-ticket-category.component';
import { TicketService } from 'src/app/api/controllers/Ticket';
import { Subscription } from 'rxjs';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';

@Component({
  selector: 'app-common-category',
  templateUrl: './common-category.component.html',
  styleUrls: ['./common-category.component.scss']
})
export class CommonCategoryComponent implements OnInit {
  @ViewChild('commondatagrid', { static: false }) commondatagrid: jqxGridComponent;
  commonColumns:any;
  commonFilter: string = "";
  commonListData: any;
  isDataLoaded:boolean = true;
  totalItems:number = 0;
  apiSubscribe:Subscription;
  modalService:any;

  constructor( 
    public dialog: MatDialog,
    private sharedService:SharedService,
    private ticketService: TicketService,
    private sessionService:SessionService,
    private injector: Injector) {
      this.modalService = this.injector.get(ModalService);
    }

  addTicket(mode,categoryId) {
    let data:any= {type:'common',mode,categoryId};
    const dialogRef = this.dialog.open(AddTicketCategoryComponent, {
      panelClass: 'material-dialog-medium',
      data: data
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.getCommonCategoryList();
      }
    });
  }

  @HostListener('window:onCommonCatEdit', ['$event.detail'])
  onCommonCatEdit(detail) {
    let dataRecord = this.commondatagrid.getrowdata(detail.rowId);
    this.addTicket('edit',dataRecord.ticketCategoryId)
  }


  @HostListener('window:onCommonCatDelete', ['$event.detail'])
  onCommonCatDelete(detail) {
    let dataRecord = this.commondatagrid.getrowdata(detail.rowId);
    this.modalService.showConfirmModal({id:dataRecord.ticketCategoryId,index:detail.rowId});
  }

  commonSearch() {
    if (this.commonFilter != "") {
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.commonFilter;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.commondatagrid.showfiltercolumnbackground(false);
      this.commonColumns.forEach(item => {
        if (item.datafield != 'Actions') {
          this.commondatagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.commondatagrid.applyfilters();
    }
    else {
      this.commondatagrid.clearfilters();
    }
  }

  getCommonCategoryList() {
    this.isDataLoaded = true;
    let params = {
      apartmentId: this.sessionService.apartmentId,
      ticketTypeId: 24,   //Common TicketType Id
    }
    this.ticketService.getAllTicketManagerByApartmentId(params).subscribe((res:any)=>{
      if(res.length > 0) {
        this.totalItems = res.length;
        let data = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.commonListData = new jqx.dataAdapter(data);
      }
      this.isDataLoaded = false;
    })
  }

  ngOnInit(): void {
    this.getCommonCategoryList();
    var cellsrendererCommon = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrendererCommon = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.commonColumns = [{
      text: 'Category',
      datafield: 'ticketCategoryName',
      width: 200,
      pinned: true,
      cellsrenderer: cellsrendererCommon,
      renderer: columnrendererCommon
    },{
      text: 'Supervisor',
      datafield: "escalationLevelId0",
      cellsrenderer:  (row, column, value) => {
        let field = this.commonListData.loadedData[row].escalationLevelId0;
        if(field && field.userName) 
          value = field.userName;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 80,
      renderer: columnrendererCommon
    },{
      text: 'Level-1',
      datafield: 'escalationLevelId1',
      cellsrenderer:  (row, column, value) => {
        let field = this.commonListData.loadedData[row].escalationLevelId1;
        if(field && field.userName) 
          value = field.userName;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 170,
      renderer: columnrendererCommon
    },{
      text: 'L1 escdays',
      cellsrenderer:  (row, column, value) => {
        let field = this.commonListData.loadedData[row].escalationLevelId1;
        if(field && field.escalationDays) 
          value = field.escalationDays;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 80,
      renderer: columnrendererCommon
    },{
      text: 'Level-2',
      datafield: 'escalationLevelId2',
      cellsrenderer:  (row, column, value) => {
        let field = this.commonListData.loadedData[row].escalationLevelId2;
        if(field && field.userName) 
          value = field.userName;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 170,
      renderer: columnrendererCommon
    },{
      text: 'L2 escdays',
      cellsrenderer:  (row, column, value) => {
        let field = this.commonListData.loadedData[row].escalationLevelId2;
        if(field && field.escalationDays) 
          value = field.escalationDays;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 170,
      renderer: columnrendererCommon
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editCommonCatEvent(' + row + ')"><i class="fa fa-pencil icon edit" title="Edit Ticket Details" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmDeleteEventCommon(' + row + ')"><i class="fa fa-trash icon delete" title="Delete" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrendererCommon
    }];

     //Delete Category
		this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe( item => {
			if(item != null && item.id) {
        let params = {
          apartmentId: this.sessionService.apartmentId,
          ticketCategoryId: item.id,
          deleteBy: this.sessionService.userId
        }
        this.ticketService.deleteTicketManagerByTicketCategoryId(params).subscribe((res:any) => {
          if(res.message) {
            this.sharedService.setUnitListDeleteIndex(null);
            this.commondatagrid.deleterow(item.index);
          }
        });
      }
		})
  }

  ngOnDestroy(){
		this.apiSubscribe.unsubscribe();
	}
}

function showConfirmDeleteEventCommon(row) {
  var event = new CustomEvent('onCommonCatDelete', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDeleteEventCommon = showConfirmDeleteEventCommon;


function editCommonCatEvent(row) {
  var event = new CustomEvent('onCommonCatEdit', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editCommonCatEvent = editCommonCatEvent;
