import { Component, OnInit } from '@angular/core';

import { ApartmentService } from 'src/app/api/controllers/Apartment';
import { LookupService } from 'src/app/api/controllers/Lookup';
import { SharedService } from 'src/app/shared/services/shared.service';
import { SessionService } from 'src/app/core/session/session.service';
import { StaffService } from 'src/app/api/controllers/Staff';
import { TicketService } from 'src/app/api/controllers/Ticket';

import * as _ from 'underscore';
import * as moment from 'moment';

@Component({
  selector: 'app-open-tickets-report',
  templateUrl: './open-tickets-report.component.html',
  styleUrls: ['./open-tickets-report.component.scss']
})
export class OpenTicketsReportComponent implements OnInit {

  blockData:any;
  blockUnitData:any;
  ticketTypeList: any;
  ticketStatusList: any;
  ticketPriortyList: any;
  staffDataList: any;

  report: any;
  openTicketReport:any;

  glAccountsDataList:any;
  glDocumentTypeDataList:any;

  isBlockSelected:boolean = false;
  isUnitSelected:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 8;

  unitFieldType:string = "unitno";
  unitOrder:boolean = true;

  isReportSubmitted:boolean = true;
  isDataLoaded:boolean = true;

  reportsData:any;


  public ticketTypeParams = {
    LookupTypeId: 7
  }

  public priortyParams = {
    LookupTypeId: 9
  }

  public statusParams = {
    LookupTypeId: 14
  }

  public ticketIDParam: any;

  public apartmentParams = {
    apartmentId: this.sessionService.apartmentId
  }

  constructor(
  	private apartmentService:ApartmentService,
  	private sharedService:SharedService,
    private staffService: StaffService,
    private ticketService: TicketService,
  	private lookupService:LookupService,
  	private sessionService:SessionService
  ) { }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.unitFieldType = type;
    this.unitOrder = !this.unitOrder;
  }

  getFieldOrderBy(type) {
    if(this.unitFieldType == type){
      return this.unitOrder ? 'asc' : 'desc';
    }
    else return '';
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  getDate(date){
    if(date){
    return moment(date).format("MM-DD-YYYY");
    }
  }

  getType(type_id){
    let type =  this.ticketTypeList.find(x=> x.lookupValueId === type_id);
     if(type){
      return type.lookupValueName;
     }
  }

  getDateFormat(date){
    if(date){
    return moment(date).format("YYYY-MM-DD");
    }
  }

  getTimeFormat(dateTime){
    return moment(dateTime).format("YYYY-MM-DD HH:mm");
  }

  submitOpenTicketForm(form) {

  	this.isReportSubmitted = true;
    this.isDataLoaded = false;
    this.ItemStartIndex = 0;


  	let details = {
  	  ApartmentID: this.sessionService.apartmentId,
  	  ApartmentBlockID: parseInt(this.report.apartmentBlockId),
  	  StartDate: this.getDateFormat(this.report.fromDate),
  	  EndDate: this.getDateFormat(this.report.toDate),
      TicketStatus: parseInt(this.report.status),
      TicketType: parseInt(this.report.type),
      AssignedToStaffId: parseInt(this.report.assignedStaff),
    }


  	this.ticketService.getReportsForListofOpenCloseTicketsMultiFilter(details).subscribe((res:any) => {

    	this.openTicketReport = res;

    	this.totalItems = this.openTicketReport.length;

		if(this.totalItems>this.itemLimit){
			this.ItemEndIndex = this.itemLimit;
		}
		else {
			this.ItemEndIndex = this.totalItems;
		}

		this.isDataLoaded = true;
		this.isReportSubmitted = false;

    },
    error => {

    });

  }

  ngOnInit() {

  	this.report = {};
  	this.report.apartmentBlockId = "";
  	this.report.apartmentBlockUnitId = "";

  	this.apartmentService.getApartmentBlockByApartmentId(this.apartmentParams).subscribe((res:any) => {
      this.blockData = res;
    });

    this.lookupService.getLookupValueByLookupTypeId(this.ticketTypeParams).subscribe((res:any) => {
      this.ticketTypeList = res;
    });

    this.lookupService.getLookupValueByLookupTypeId(this.statusParams).subscribe((res:any) => {
      this.ticketStatusList = res;
    });

    this.staffService.getAllStaffsByApartmentId(this.apartmentParams).subscribe((res:any) => {
      this.staffDataList = res;
      this.staffDataList.sort((a, b) => a.firstName.localeCompare(b.firstName))
    });

    this.lookupService.getLookupValueByLookupTypeId(this.priortyParams).subscribe((res:any) => {
      this.ticketPriortyList = res;
    },
    error => {

    });

  }

}
