import { Component, OnInit, ViewChild, HostListener, Injector } from '@angular/core';
import { SessionService } from 'src/app/core/session/session.service';
import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { Router } from '@angular/router';
import { MatDialog } from '@angular/material/dialog';
import { AddTicketCategoryComponent } from '../add-ticket-category/add-ticket-category.component';
import { TicketService } from 'src/app/api/controllers/Ticket';
import { Subscription } from 'rxjs';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';

@Component({
  selector: 'app-private-category',
  templateUrl: './private-category.component.html',
  styleUrls: ['./private-category.component.scss']
})
export class PrivateCategoryComponent implements OnInit {
  @ViewChild('privatedatagrid', { static: false }) privatedatagrid: jqxGridComponent;
  privateColumns: any;
  privateFilter: string;
  privateListData: any;
  isDataLoaded:boolean = true;
  totalItems:number = 0;
  apiSubscribe:Subscription;
  modalService:any;

  constructor(
    public dialog: MatDialog,
    private sharedService:SharedService,
    private ticketService: TicketService,
    private injector: Injector,
    private sessionService:SessionService) {
      this.modalService = this.injector.get(ModalService);
    }

  addTicket(mode,categoryId) {
    let data:any= {type:'private',mode,categoryId};
    const dialogRef = this.dialog.open(AddTicketCategoryComponent, {
      panelClass: 'material-dialog-medium',
      data: data
    });
    dialogRef.afterClosed().subscribe(result => {
      if(result) {
        this.getPrivateCategoryList();
      }
    });
  }

  @HostListener('window:onPrivateCatEdit', ['$event.detail'])
  onPrivateCatEdit(detail) {
    let dataRecord = this.privatedatagrid.getrowdata(detail.rowId);
    this.addTicket('edit',dataRecord.ticketCategoryId)
  }

  @HostListener('window:onPrivateCatDelete', ['$event.detail'])
  onPrivateCatDelete(detail) {
    let dataRecord = this.privatedatagrid.getrowdata(detail.rowId);
    this.modalService.showConfirmModal({id:dataRecord.ticketCategoryId,index:detail.rowId});
  }

  privateSearch() {
    if (this.privateFilter != "") {
      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.privateFilter;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);
      this.privatedatagrid.showfiltercolumnbackground(false);
      this.privateColumns.forEach(item => {
        if (item.datafield != 'Actions') {
          this.privatedatagrid.addfilter(item.datafield, filtergroup, true);
        }
      })
      this.privatedatagrid.applyfilters();
    }
    else {
      this.privatedatagrid.clearfilters();
    }
  }

  getPrivateCategoryList() {
    this.isDataLoaded = true;
    let params = {
      apartmentId: this.sessionService.apartmentId,
      ticketTypeId: 27,   //Private TicketType Id
    }
    this.ticketService.getAllTicketManagerByApartmentId(params).subscribe((res:any)=>{
      if(res.length > 0) {
        this.totalItems = res.length;
        let data = {
          localdata: res.reverse(),
          datatype: "array"
        }
        this.privateListData = new jqx.dataAdapter(data);
      }
      this.isDataLoaded = false;
    })
  }

  ngOnInit(): void {
    this.getPrivateCategoryList();
    var cellsrendererPrivate = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }
    var columnrendererPrivate = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }
    this.privateColumns = [{
      text: 'Category',
      datafield: 'ticketCategoryName',
      width: 200,
      pinned: true,
      cellsrenderer: cellsrendererPrivate,
      renderer: columnrendererPrivate
    },{
      text: 'Supervisor',
      datafield: "escalationLevelId0",
      cellsrenderer:  (row, column, value) => {
        let field = this.privateListData.loadedData[row].escalationLevelId0;
        if(field && field.userName) 
          value = field.userName;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 80,
      renderer: columnrendererPrivate
    },{
      text: 'Level-1',
      datafield: 'escalationLevelId1',
      cellsrenderer:  (row, column, value) => {
        let field = this.privateListData.loadedData[row].escalationLevelId1;
        if(field && field.userName) 
          value = field.userName;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 170,
      renderer: columnrendererPrivate
    },{
      text: 'L1 escdays',
      cellsrenderer:  (row, column, value) => {
        let field = this.privateListData.loadedData[row].escalationLevelId1;
        if(field && field.escalationDays) 
          value = field.escalationDays;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 80,
      renderer: columnrendererPrivate
    },{
      text: 'Level-2',
      datafield: 'escalationLevelId2',
      cellsrenderer:  (row, column, value) => {
        let field = this.privateListData.loadedData[row].escalationLevelId2;
        if(field && field.userName) 
          value = field.userName;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 170,
      renderer: columnrendererPrivate
    },{
      text: 'L2 escdays',
      cellsrenderer:  (row, column, value) => {
        let field = this.privateListData.loadedData[row].escalationLevelId2;
        if(field && field.escalationDays) 
          value = field.escalationDays;
        else 
          value = '';
        return '<div class="jqx-custom-inner-cell">' + value + '</div>';
      },
      minwidth: 170,
      renderer: columnrendererPrivate
    },{
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {
        return '<div class="simple-actions"><a href="javascript:void(0)" class="mr-2" onClick="editPrivateCatEvent(' + row + ')"><i class="fa fa-pencil icon edit" title="Edit Ticket Details" aria-hidden="true"></i></a><a href="javascript:void(0)" class="mr-2" onClick="showConfirmDeleteEventPrivate(' + row + ')"><i class="fa fa-trash icon delete" title="Delete" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrendererPrivate
    }];

      //Delete Category
		this.apiSubscribe = this.sharedService.unitlistdeleteindexcast.subscribe( item => {
			if(item != null && item.id) {
        let params = {
          apartmentId: this.sessionService.apartmentId,
          ticketCategoryId: item.id,
          deleteBy: this.sessionService.userId
        }
        this.ticketService.deleteTicketManagerByTicketCategoryId(params).subscribe((res:any) => {
          if(res.message) {
            this.sharedService.setUnitListDeleteIndex(null);
            this.privatedatagrid.deleterow(item.index);
          }
        });
      }
		})
  }

  ngOnDestroy(){
		this.apiSubscribe.unsubscribe();
	}
}

function showConfirmDeleteEventPrivate(row) {
  var event = new CustomEvent('onPrivateCatDelete', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).showConfirmDeleteEventPrivate = showConfirmDeleteEventPrivate;

function editPrivateCatEvent(row) {
  var event = new CustomEvent('onPrivateCatEdit', {
    detail: {
      rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editPrivateCatEvent = editPrivateCatEvent;

