import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { HelpdeskSetupComponent } from './components/helpdesk-setup/helpdesk-setup.component';
import { HelpdeskReportsComponent } from './components/helpdesk-reports/helpdesk-reports.component';
import { PrivateCategoryComponent } from './components/helpdesk-setup/private-category/private-category.component';
import { CommonCategoryComponent } from './components/helpdesk-setup/common-category/common-category.component';
import { HelpDeskCreateTicketComponent } from 'src/app/shared/components/help-desk-create-ticket/help-desk-create-ticket.component';
import { HelpdeskTicketFilterComponent } from './components/helpdesk-ticket-filter/helpdesk-ticket-filter.component';

const routes: Routes = [
	{ path: '', redirectTo: 'settings', pathMatch: 'full'},
	{ path: 'settings', component: HelpdeskSetupComponent,
		children: [
			{ path: '', redirectTo: 'private-category', pathMatch: 'full'},
			{ path: 'private-category', component: PrivateCategoryComponent},
			{ path: 'common-category', component: CommonCategoryComponent}, 
			{ path: '**', redirectTo: 'private-category', pathMatch: 'full'}
		]
	},
	{ path: 'create-ticket', component: HelpDeskCreateTicketComponent},
	{ path: 'edit-ticket/:id', component: HelpDeskCreateTicketComponent},
	{ path: 'unassigned',  component: HelpdeskTicketFilterComponent},
	{ path: 'open-tickets', component:HelpdeskTicketFilterComponent},
	{ path: 'closed-tickets', component:HelpdeskTicketFilterComponent},
	{ path: 'assigned-to-me',  component: HelpdeskTicketFilterComponent},
	{ path: 'all-tickets', component: HelpdeskTicketFilterComponent},
	{ path: 'reports', component: HelpdeskReportsComponent},
	{ path: '**', redirectTo: 'settings', pathMatch: 'full'}
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class HelpdeskRoutingModule { }
